h1{
    background-color: #fff;
    font-size: 50px;
    padding: 20px 40px;
    border-radius: 20px;
    border-radius: 34% 66% 80% 20% / 28% 23% 77% 72% ;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; 
}

button{
    padding: 10px 20px;
    border: none;
    margin: 10px;
    width: 150px;
    letter-spacing:1.2px ;
    cursor: pointer;
    transition: 2s;
    border-radius: 20px;
}

button:hover{
    background-color: black;
    color: #fff;
}